<script>
export default {
	name: 'mapthematics',
	data: ()=>{
		return {
			modalStyle: 'display: none;',
			modalTitle: '',
			modalContent: '',
			modalData: [{
				'title': 'Liczenie',
				'content': `<p>Zaczątki matematyki leżą w liczeniu.</p><p>Tak naprawdę liczenie nie jest tylko atrybutem człowieka, inne zwierzęta też umieją liczyć, a dowody na dowody na arytmetykę człowieka są już w czasach prehistorycznych znakami robionymi na kościach.</p><p>Mieliśmy kilka innowacji z Egipcjanami posiadającymi ich pierwsze równanie, a starożytni Grecy bardzo rozwinęli geometrię i numerologię, a liczby ujemne "wynaleziono" w Chinach. Zero jako liczba po raz pierwszy było użyte w Indiach. W Złotej Erze Islamu Persowie poczynili kolejne postępy w pierwszej książce o algebrze.</p>`
			},{
				'title': 'Liczby',
				'content': `<p>Nauka matematyka zaczyna się od liczb naturalnych i co można z nimi zrobić. Jak np. operacje arytmetyczne na nich wykonywać.</p><p>Dalej, patrzy na inne rodzaje liczb jak np. całkowite, które zawierają liczby ujemne. ułamki, liczby rzeczywiste jak Pi, które mają nieskończone rozwinięcie dziesiętne i jeszcze dalej jak liczby zespolone i przeróżne inne.</p>`
			},
			{
				'title': 'Liczby',
				'content': `<p>Niektóre z liczb mają ciekawe właściwości jak liczby pierwsze, Pi czy e będące podstawą logarytmu naturalnego. Są też inne właściwości tych systemów liczbowych, np. pomimo tego, że jest nieskończenie wele zarówno liczb naturalnych, jak i rzeczywistych, to jednak liczb rzeczywistych jest więcej.</p><p>Tak więc niektóre nieskończoności są większe od innych.</p>`
			},
			{
				'title': 'Liczby',
				'content': `<p>Niektóre z liczb mają ciekawe właściwości jak liczby pierwsze, Pi czy e będące podstawą logarytmu naturalnego. Są też inne właściwości tych systemów liczbowych, np. pomimo tego, że jest nieskończenie wele zarówno liczb naturalnych, jak i rzeczywistych, to jednak liczb rzeczywistych jest więcej.</p><p>Tak więc niektóre nieskończoności są większe od innych.</p>`
			},
			{
				'title': 'Struktury',
				'content': `<p>Nauka o strukturach to etap w którym zaczynamy używać liczb w równaniach w postaci zmiennych.</p><p>Algebra zawiera zasady tego jak manipulować owymi równaniami. Znajdziemy tu również wektory i macierze, które są jakby wielowymiarowymi liczbami, a zasady je opisujące między sobą są zawarte w algebrze liniowej. Teoria liczb studiuje cechy wszystkiego w ostatniej sekcji o liczbach jak właściwości liczb pierwszych.</p>`
			},
			{
				'title': 'Struktury',
				'content': `<p>Kombinatoryka patrzy na własności pewnych struktur jak drzewa, grafy i czy inne rzeczy zrobione z dyskretnych kawałków, które można policzyć.</p><p>Teoria grup patrzy na obiekty, które są w relacji do siebie w, no cóż, grupach. Znanym przykładem jest kostka Rubika, będąca przykładem permutacji grup. </p><p>Teoria porządku zajmuje się ułożeniem obiektów poprzez przestrzeganie jakichś zasad, np. czegoś jest więcej niż innych rzeczy. Liczby naturalne są przykładem uporządkowanego zbioru obiektów, ale cokolwiek z dowolną relacją wobydwie strony może być uporządkowane.</p>`
			},
			{
				'title': 'Matematyka przestrzenna',
				'content': `<p>Inna część czystej matematyki patrzy na kształty i jak one się zachowują w przestrzeni. Początek tego jest w geometrii, zawierającej Pitagorasa i jest bliska trygonometrii, z którą wszyscy jesteśmy zaznajomieni ze szkoły.</p><p>Mamy tu też ciekawe rzeczy jak fraktale, które są matematycznymi wzorami niezmienającymi się pod wpływem skali, co znaczy, że można je powiększać w nieskończoność i zawsze będą wyglądać tak samo lub podobnie.</p>`
			},
			{
				'title': 'Matematyka przestrzenna',
				'content': `<p>Topologia zajmuje się różnymi własnościami przestrzeni, gdzie możemy deformować obiekty w sposób ciągły - bez przerywania, ani łączenia ich. Np. Wstęga Moebiusa ma tylko jedną powierzchnię i jedną krawędź, bez względu na jej deformację. A filiżanki do kawy i pączki są tą samą rzeczą - mówiąc topologicznie.</p><p> Teoria miary to sposób na przypisanie wartości przestrzeniom lub zbiorom łączące razem liczby i przestrzenie</p><p>I w końcu - Gemetria różniczkowa patrzy na własności kształtów na krzywych przestrzeniach, np. trójkąty mają inne kąty na krzywej płaszczyźnie, a to przenosi nas do następnego działu - zmiany.</p>`
			},
			{
				'title': 'Zmiany',
				'content': `<p>Nauka o zmianach zawiera analizę matematyczną, opierającą się na całkach i pochodnych analizuje powierzchnię "zajmowaną" przez funkcję czy zachowanie jej gradientów. A analiza wielu zmiennych patrzy na te rzeczy pod kątem wektorów.</p>`
			},
			{
				'title': 'Zmiany',
				'content': `<p>Nauka o zmianach zawiera analizę matematyczną, opierającą się na całkach i pochodnych analizuje powierzchnię "zajmowaną" przez funkcję czy zachowanie jej gradientów. A analiza wielu zmiennych patrzy na te rzeczy pod kątem wektorów.</p><p>Znajdziemy tu też dużo innych rzeczy jak systemy dynamiczne, które analizują jak systemy te zmieniają się np. przepływ płynów czy system ze sprzężeniem zwrotnym jak ekosystemy.</p><p> Teoria chaosu zajmuję się analizą systemów dynamicznych bardzo podatnych na zmienne warunki początkowe.</p><p>W końcu: analiza zespolona patrzy na własności funkcji zmiennej zespolonej.</p>`
			},
			{
				'title': 'Podstawy',
				'content': `<p>Czy istnieje kompletny zbiór fundamentalnych reguł, zwanymi aksjomatami, z których cała matematyka pochodzi ? I czy możemy udowodnić, że są one wszystkie spójne ze sobą ? Logika matematyczna, teoria zbiorów i kategorii próbują na to odpowiedzieć i znanym rezultatem w logice matemtycznej są twierdzenia Godela o niekompletności, które dla więszkości ludzi, znaczą, że matematyka nie ma kompletnego i spójnego zbioru aksjomatów, co znaczyłoby, że to wszystko jest trochęjakby wymyślone przez nas ludzi.</p><p>A to byłoby dziwne, gdyż jak widzimy, matematyka potrafi wyjaśnić BARDZO dużo o naszym wszechświecie i to często w zaskakująco piękny sposób.</p><p>Dlaczego rzecz wymyślona przez ludzi miałaby być do tego zdolna ? To jest głęboka tajemnica... <p>Mamy też teorię obliczeń zajmującą się różnymi modelami wyliczeń jak bardzo efektywnie można rozwiązywać problemy i która zawiera teorię kompleksową zajmującą się tym, co jest, a co nie jest obliczalne, oraz ile pamięci i czasu potrzeba by do rozwiązania (do większości ciekawych problemów - szalenie dużo).</p>`
			},
			{
				'title': 'Fizyka Matematyczna',
				'content': `<p>Warto wspomnieć w tym momencie, że wszystko tu jest powiązane ze sobą dużo mocniej niż to jest narysowane. Tak naprawdę ta mapa powinna wyglądać jak sieć spinająca ze sobą wiele różnych działów</p><p>Dobrze, zacznijmy od fizyki, która używa właściwie każdego działu z lewej strony do pewnego stopnia. Matematyczna i teoretyczna fizyka ma bardzo bliski związek z czysto abstrakcyjną matematyką.</p>`
			},
			{
				'title': 'Matematyka chemiczna',
				'content': `<p>Matematyka jest też używana w innych naukach przyrodniczych razem z chemią matematyczną i biomatematyką, która zajmuje się wieloma tematami od modelowania molekuł do biologii ewolucyjnej.</p>`
			},
			{
				'title': 'Biomatematyka',
				'content': `<p>Matematyka jest też używana w innych naukach przyrodniczych razem z chemią matematyczną i biomatematyką, która zajmuje się wieloma tematami od modelowania molekuł do biologii ewolucyjnej.</p>`
			},
			{
				'title': 'Analiza Numeryczna',
				'content': `<p>Analiza numeryczna jest narzędziem matematycznym używanym w miejscach, gdzie matematyka staje się zbyt pracochłonna lub wręcz niemożliwa do całkowitego rozwiązania. </p><p>Wtedy używa się dużo prostych przybliżeń i łączy się je, by otrzymać wystarczająco dobrą odpowiedź.</p><p>Np. jeśli wpiszemy okrąg w kwadrat i porzucamy rzutki w ten kwadrat, a nstępnie porównamy liczbę rzutek w okręgu z tą w kwadracie, możemy w przybliżeniu podać wartość Pi. Oczywiście, w praktyce analiza numeryczna jest robiona na potężnych komputerach.</p>`
