			{
				'title': 'Optymalizacja',
				'content': `<p>Optymalizacja, gdzie chcemy wyliczyć najlepszy wybór spośród zbioru wielu różnych opcji czy ograniczeń, co można zwizualizować jako próbę znalezienia najmniejszego czy największego punktu funkcji. </p><p>Problem optymalizacji jest drugą naturą dla nas ludzi, robimy to bez przerwy: próbując kupić jak najwięcej za najmniej czy jakoś zmaksymalizować swoje szczęście.</p>`
			},
			{
				'title': 'Informatyka',
				'content': `<p>Innym obszarem głęboko związanym z matematyką abstrakcyjną jest informatyka a jej zasady zostały wyprowadzone z czystej matematyki i są kolejnym przykładem czegoś, co było wymyślone zanim komputery zostały zbudowane.</p><p>Uczenie maszynowe: powstanie inteligentnych komputerów używa wielu obszarów w matematyce jak algebra liniowa, optymalizacja, systemy dynamiczne czy prawdopodobieństwo.</p>`
			},
			{
				'title': 'Kryptografia',
